import axios from 'axios';
import router from '@/router';
import cache from './cache';

// 创建 axios 实例
const instance = axios.create({
  baseURL: process.env.VUE_APP_API_URL || 'http://localhost:5000/api',
  timeout: 10000
});

// 请求队列和防抖处理
const pendingRequests = new Map();

const generateKey = (config) => {
  const { url, method, params, data } = config;
  return [url, method, JSON.stringify(params), JSON.stringify(data)].join('&');
};

// 请求拦截器
instance.interceptors.request.use(
  config => {
    // 从 localStorage 获取 token
    const token = localStorage.getItem('token');
    const tokenExpires = localStorage.getItem('tokenExpires');
    
    // 检查 token 是否有效
    if (token && tokenExpires && Date.now() < parseInt(tokenExpires)) {
      config.headers['Authorization'] = `Bearer ${token}`;
    }

    // 处理重复请求
    const requestKey = generateKey(config);
    if (pendingRequests.has(requestKey)) {
      const controller = pendingRequests.get(requestKey);
      controller.abort();
    }

    const controller = new AbortController();
    config.signal = controller.signal;
    pendingRequests.set(requestKey, controller);

    // 处理缓存
    if (config.cache) {
      const cachedData = cache.get(requestKey);
      if (cachedData) {
        return Promise.resolve(cachedData);
      }
    }

    return config;
  },
  error => {
    return Promise.reject(error);
  }
);

// 响应拦截器
instance.interceptors.response.use(
  response => {
    const requestKey = generateKey(response.config);
    pendingRequests.delete(requestKey);

    // 缓存响应
    if (response.config.cache) {
      cache.set(requestKey, response.data, response.config.cacheTTL);
    }

    return response;
  },
  error => {
    if (error.response?.status === 401) {
      // 清除无效的 token
      localStorage.removeItem('token');
      localStorage.removeItem('tokenExpires');
      
      // 如果不是登录页面，则重定向到登录页
      if (router.currentRoute.value.path !== '/login') {
        router.push('/login');
      }
    }
    return Promise.reject(error);
  }
);

export default instance; 